// 头像绑定账号返回随机色
Vue.prototype.getcolor = (account) => {
  function getcolor(account){
    let obj = {
      start:'#9DD1F9',
      end: '#9FC6FF'
    }
    if(!account) return obj
    let arrColor = [
      {
        start:'#9DD1F9',
        end: '#9FC6FF'
      },
      {
        start:'#9DB0F9',
        end: '#9FB5FF'
      },
      {
        start:'#657AEA',
        end: '#6689FF'
      },
      {
        start:'#31A2FA',
        end: '#49BCEB'
      },
      {
        start:'#8976FF',
        end: '#8976FF'
      },
      {
        start:'#C982C8',
        end: '#D793E2'
      },
      {
        start:'#43BABB',
        end: '#46B7B8'
      },
      {
        start:'#6ACFA3',
        end: '#72C1A1'
      },
      {
        start:'#A6774A',
        end: '#BD9070'
      },
      {
        start:'#FFCB4C',
        end: '#FFC57B'
      }
    ]
    let asciiSum = 0;
    for (let i = 0; i < account.length; i++) {
      asciiSum += account[i].charCodeAt();
    }
    obj = arrColor[asciiSum % arrColor.length];
    return obj
  }
  let obj = getcolor(account)
  return "linear-gradient("+obj.start+","+obj.end+")"
}

// 多行文本居中
Vue.prototype.lineClamp = (n) => {
  return "display:-webkit-box;overflow:hidden;text-overflow:ellipsis;-webkit-line-clamp:"+n+";-webkit-box-orient:vertical;"
}
window.OpenLogDetail = process.env.OpenLogDetail;

// 路由权限控制
router.beforeEach((to, from, next) => {
  if(to.meta.requireAuth){
    console.log('路由权限控制', store)
    let userInfo = window.sessionStorage.getItem("LoginServe")
    userInfo = userInfo && typeof userInfo === "string" ? JSON.parse(userInfo) : userInfo;
    if(userInfo && userInfo.UserId){
      next();
    }else{
      if(/confId=/.test(to.fullPath)){
        next({path:"/", query:{confId: to.fullPath.split('confId=')[1]}})
      }else{
        next({path:"/"})
      }
    }
  }else{
    next();
  }
})

Vue.prototype.globalClick = function (callback) {
  document.getElementById('app').onclick = function () {
    callback&&callback()
  };
};

Vue.prototype.setFontSize = function (fontSize) {
  // let fontSize = window.CurrentUser.systemSettings.FontSize
  var docEl = document.documentElement,resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
  recalc = function(){
    let scaleNum = fontSize/100
    if(!scaleNum) return;
    if(scaleNum == 1){
      docEl.style.fontSize = '1px';
    } else{
      docEl.style.fontSize = 1 + (scaleNum - 1) * 3 / 4 + 'px';
    }
  };
  recalc()
};
