<template>
  <div class="box">
    <div class="login"
         :style="{backgroundImage:'url(' + $root.imgList['bjH5'] + ')'}">
      <div class="imgCon">
        <img :src="$root.imgList['tu']"
             alt="">
      </div>
      <div class="container"
           :style="{backgroundImage:'url(' + $root.imgList['bg_white'] + ')'}">
        <div class="imgPart">
          <p class="logo">
            <img :src="$root.imgList['logoH5']"
                 alt="">
            <span class="icon-rongxin-login"
                  :style="{backgroundImage:'url(' + $root.imgList['rongxin_mainColor'] + ')'}"></span>
          </p>
        </div>
        <div class="main_container">
          <el-form :model="login"
                   :rules="loginRules"
                   ref="loginRef"
                   style="padding-top:23px">
            <el-form-item prop="account">
              <el-input v-model.trim="login.account"
                        minlength="4"
                        oninput="value=value.replace(/[^\d]/g,'')"
                        prefix-icon="RSIcon RSIcon-huiyima col2A"
                        placeholder="请输入账号"></el-input>
            </el-form-item>
            <el-form-item prop="userName">
              <el-input v-model.trim="login.userName"
                        prefix-icon="RSIcon RSIcon-canhuiming col2A "
                        placeholder="请输入昵称"
                        maxlength="30"
                        @keyup.enter.native="initSdk()">
              </el-input>
            </el-form-item>
          </el-form>
          <el-button type="primary"
                     class="rx_btn loginBtn active"
                     @click="initSdk()">登录</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import store from '@/store/index'
export default {
  name: 'login',
  data() {
    return {
      login: {
        account: '15071046273', //im登录账号
        userName: 'vivi', //会中昵称
      },
      IsCheckedOpenVideo: false,
      IsCheckedOpenAudio: false,
      loginRules: {
        account: [
          {
            required: true,
            message: '请输入账号',
            trigger: 'blur',
          },
          {
            min: 6,
            max: 100,
            message: '请输入6-20个字符的账号',
            trigger: 'blur',
          },
        ],
        userName: [
          {
            required: true,
            message: '请输入昵称',
            trigger: 'blur',
          },
          {
            min: 2,
            max: 30,
            message: '请输入2-30个字符的昵称',
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {
    const that = this
    window.addEventListener('message', (event) => {
      this.$root.parentModel = event
      const postMessage = event.data
      that.login = {
        account: postMessage.userId,
        userName: postMessage.userName,
      }
    })
  },
  methods: {
    ...mapMutations(['SetLoginServe', 'SET_USERACCOUNT']),
    initSdk() {
      let self = this
      this.$refs['loginRef'].validate((valid) => {
        if (valid) {
          let LoginServe = {}
          const { AppId, AppToken, ConnectorAddrs, LvsAddrs, WbssAddrs } =
            window
          LoginServe = {
            UserId: self.login.account,
            AppId,
            AppToken,
            ConnectorAddrs,
            LvsAddrs,
            WbssAddrs,
            Profile: {
              NickName: self.login.userName,
              init: true,
            },
          }

          sessionStorage.setItem('LoginServe', JSON.stringify(LoginServe))
          self.SET_USERACCOUNT(LoginServe.UserId)
          self.SetLoginServe(LoginServe)
          const routeData = this.$router.resolve('/yuntongxun')
          window.open(routeData.href, '_self')
        }
      })
    },
    setWindowData() {
      window.RL_YTX_NEW = window.parent.RL_YTX_NEW
      window.RL_Chat = window.parent.RL_Chat
      window.RL_Media = window.parent.RL_Media
      window.RL_MEET = window.parent.RL_MEET
    },
  },
  watch: {
    login: function (val) {
      this.setWindowData()
      val.account && this.initSdk()
    },
  },
}
</script>

<style scoped lang='stylus'>
.box
  position relative
  font-size 14px
  width 100%
  height 100%
  .login
    width 100%
    height 100%
    background-repeat no-repeat
    background-size cover
    .imgCon
      float left
      width 55%
      height 100%
      display flex
      align-items center
      justify-content center
    .container
      float right
      width 45%
      height 100%
      background-repeat no-repeat
      background-size cover
      box-sizing border-box
      .imgPart
        height 38%
        display flex
        padding-left 44px
        box-sizing border-box
        .logo
          display flex
          align-items center
          align-self flex-end
          .icon-rongxin-login
            margin-left 10px
            display inline-block
            width 94px
            height 42px
            background-repeat no-repeat
            background-size cover
      .main_container
        padding-left 60px
        width 415px
        .loginBtn
          width 100%
          height 40px
          margin-top 30px
          border 0
</style>



