import {
  SET_USERINFO,
  SET_USERACCOUNT,
  SET_DEPARTTREE,
  SET_ALLPERSONS,
  SET_DEPARTMENTS,
  SET_GROUPLIST,
  SET_TOPCONNECTOR,
  SET_ATTENTIONS,
  SET_CONNECTORS,
  SET_ISREADTIME,
  SET_SELECTPERSONTREE,
  SET_NOTRINGLIST,
  SET_ONLINELIST,
  SET_LEVELPERMISSION,
  SET_FRIENDACCOUNTS,
  LAST_RECEIVEID,
  SET_METTINGLIST,
  SET_MeetInfo,
  SET_WhiteBoardInfo,
  SET_MemberInObj,
  SET_MemberOutObj,
  SET_SelfMeetUser,
  SET_PlayUserList,
  SET_PhotoObj,
  SET_StreamObj,
  SET_MainVenue,
  SET_StreamIdObj,
  SET_SelfMeetUserId,
  SET_MeetingGroupInfo,
  SET_HideMsgInfo,
  SET_GroupStreamObj,
  SET_GroupStreamIdObj,
  SET_GroupMemberInObj,
  SET_GroupMeetInfo,
  SET_CompId,
  SET_AllMessages,
  SET_MeetSetting
  // type
} from './mutation-types'
//必须是同步代码
const mutation = {
  [SET_USERINFO] (state, userInfo) {
    state.user_info = userInfo
    sessionStorage.setItem("loginConfig", JSON.stringify(userInfo));
  },
  [SET_USERACCOUNT] (state, useraccount) {
    state.user_account = useraccount
  },
  [SET_DEPARTTREE] (state, departtree) {
    state.depart_tree = departtree
  },
  [SET_SELECTPERSONTREE] (state, selectpersontree) {
    state.select_persontree = selectpersontree
  },
  [SET_ALLPERSONS] (state, persons) {
    state.all_persons = persons
  },
  [SET_DEPARTMENTS] (state, departments) {
    state.depart_ments = departments
  },
  [SET_GROUPLIST] (state, list) {
    state.grouplist = list
  },
  [SET_TOPCONNECTOR] (state, list) {
    state.topconnector = list
  },
  [SET_ATTENTIONS] (state, attentions) {
    state.attentions = attentions
  },
  [SET_CONNECTORS] (state, connectors) {
    state.connectors = connectors
  },
  [SET_ISREADTIME] (state, isReadTime) {
    state.isReadTime = isReadTime
  },
  [SET_NOTRINGLIST] (state, notRingList) {
    state.notRingList = notRingList
  },
  [SET_ONLINELIST] (state, onLineList) {
    state.onLineList = JSON.stringify(onLineList)
  },
  [SET_LEVELPERMISSION] (state, levelPermission) {
    state.levelPermission = levelPermission
  },
  [SET_FRIENDACCOUNTS] (state, friendAccounts) {
    state.friendAccounts = friendAccounts
  },
  [LAST_RECEIVEID] (state, lastReceiveId) {
    state.lastReceiveId = lastReceiveId
  },
  [SET_METTINGLIST] (state, meetingList) {
    state.meetingList = meetingList
  },
  [SET_CompId] (state, compId) {
    state.compId = compId;
  },
  [SET_AllMessages] (state, allMessages) {
    state.allMessages = JSON.stringify(allMessages);
  },
  // 会议相关开始
  [SET_MeetInfo] (state, meetInfo) {
    state.meetInfo = JSON.stringify(meetInfo);
  },
  [SET_WhiteBoardInfo](state, whiteBoardInfo) {
    state.whiteBoardInfo = JSON.stringify(whiteBoardInfo)
  },
  [SET_MemberInObj](state, memberInObj) {
    state.memberInObj = JSON.stringify(memberInObj)
    // Vue.set(state, "memberInObj", JSON.stringify(memberInObj));
    console.log('SET_MemberInObj', JSON.stringify(memberInObj));
  },
  [SET_MemberOutObj](state, memberOutObj) {
    state.memberOutObj = JSON.stringify(memberOutObj)
  },
  [SET_SelfMeetUser](state, selfMeetUser) {
    state.selfMeetUser = JSON.stringify(selfMeetUser)
  },
  [SET_PlayUserList](state, PlayUserList) {
    state.PlayUserList = JSON.stringify(PlayUserList)
  },
  [SET_PhotoObj](state, photoObj) {
    state.photoObj = JSON.stringify(photoObj)
  },
  [SET_StreamObj](state, streamObj) {
      state.streamObj = streamObj;
      let obj = {};
      for (let val in streamObj) {
          obj[val] = streamObj[val].id;
      }
      state.streamIdObj = JSON.stringify(obj);
  },
  [SET_StreamIdObj](state, streamIdObj) {
      state.streamIdObj = JSON.stringify(streamIdObj)
  },
  [SET_MainVenue](state, mainVenue) {
      console.log('SET_MainVenue', mainVenue);
      state.mainVenue = mainVenue
  },
  [SET_SelfMeetUserId](state, SelfMeetUserId) {
      state.SelfMeetUserId = SelfMeetUserId
  },
  [SET_MeetingGroupInfo](state, meetingGroupInfo) {
      state.meetingGroupInfo = meetingGroupInfo
  },
  [SET_HideMsgInfo](state, hideMsgInfo) {
      state.hideMsgInfo = JSON.stringify(hideMsgInfo)
  },
  [SET_MeetSetting](state, meetSetting) {
      state.meetSetting = JSON.stringify(meetSetting)
  },
  // 会议相关结束

  //九宫格会议

    [SET_GroupMeetInfo] (state, groupMeetInfo) {
        state.groupMeetInfo = JSON.stringify(groupMeetInfo);
    },
    [SET_GroupMemberInObj](state, groupMemberInObj) {
        state.groupMemberInObj = JSON.stringify(groupMemberInObj)
        console.log('SET_GroupMemberInObj', JSON.stringify(groupMemberInObj));
    },
    [SET_GroupStreamObj](state, groupStreamObj) {
        state.groupStreamObj = groupStreamObj;
        let obj = {};
        for (let val in groupStreamObj) {
            obj[val] = groupStreamObj[val].id;
        }
        state.groupStreamIdObj = JSON.stringify(obj);
    },
    [SET_GroupStreamIdObj](state, groupStreamIdObj) {
        state.groupStreamIdObj = JSON.stringify(groupStreamIdObj)
    },
}
export default mutation



