//相当于store的计算属性
//Getter 也可以接受其他 getter 作为第二个参数：
const getters = {
    user_info (state) {
        return state.user_info
    },
    user_account (state) {
        return state.user_account
    },
    all_persons (state) {
        return state.all_persons
    },
    depart_ments (state) {
        return state.depart_ments
    },
    depart_tree (state) {
        return state.depart_tree
    },
    select_persontree (state) {
        return state.select_persontree
    },
    grouplist (state) {
        return state.grouplist
    },
    topconnector (state) {
        return state.topconnector
    },
    attentions (state) {
        return state.attentions
    },
    connectors (state) {
        return state.connectors
    },
    isReadTime (state) {
        return state.isReadTime
    },
    notRingList (state) {
        return state.notRingList
    },
    onLineList (state) {
        return JSON.parse(state.onLineList)
    },
    levelPermission (state) {
        return state.levelPermission
    },
    friendAccounts(state){
        return state.friendAccounts
    },
    lastReceiveId(state){
        return state.lastReceiveId
    },
    meetingList(state){
        return state.meetingList
    },
    allMessages: (state) => {
        return JSON.parse(state.allMessages)
    },
    // 会议相关开始
    getMemberInById: (state) => (id) => {
        return JSON.parse(state.memberInObj)[id]
    },
    meetInfo: (state) => {
        return JSON.parse(state.meetInfo)
    },
    whiteBoardInfo: (state) => {
        return JSON.parse(state.whiteBoardInfo)
    },
    memberInObj: (state) => {
        return JSON.parse(state.memberInObj)
    },
    memberOutObj: (state) => {
        return JSON.parse(state.memberOutObj)
    },
    selfMeetUser: (state) => {
        return JSON.parse(state.selfMeetUser)
    },
    PlayUserList: (state) => {
        return JSON.parse(state.PlayUserList)
    },
    photoObj: (state) => {
        return JSON.parse(state.photoObj)
    },
    streamIdObj: (state) => {
        return JSON.parse(state.streamIdObj)
    },
    getStreamId: (state) => (id) => {
        return JSON.parse(state.streamIdObj)[id]
    },
    streamObj: (state) => {
        return state.streamObj
    },
    getStreamById: (state) => (id) => {
        return state.streamObj[id]
    },
    mainVenue: (state) => {
        return state.mainVenue
    },
    SelfMeetUserId: (state) => {
        return state.SelfMeetUserId
    },
    meetingGroupInfo: (state) => {
        return state.meetingGroupInfo
    },
    hideMsgInfo: (state) => {
        return JSON.parse(state.hideMsgInfo)
    },
    meetSetting: (state) => {
        return JSON.parse(state.meetSetting)
    },
  // 会议相关结束

    //九宫格会议
    groupMeetInfo: (state) => {
        return JSON.parse(state.groupMeetInfo)
    },
    getGroupMemberInById: (state) => (id) => {
        return JSON.parse(state.groupMemberInObj)[id]
    },
    groupMemberInObj: (state) => {
        return JSON.parse(state.groupMemberInObj)
    },
    groupStreamIdObj: (state) => {
        return JSON.parse(state.groupStreamIdObj)
    },
    getGroupStreamId: (state) => (id) => {
        return JSON.parse(state.groupStreamIdObj)[id]
    },
    groupStreamObj: (state) => {
        return state.groupStreamObj
    },
    getGroupStreamById: (state) => (id) => {
        console.log('state.groupMemberInObj',state.groupStreamObj);
        return state.groupStreamObj[id]
    },
    getCompId (state) {
        return state.compId
    },
}
export default getters



