
export default {
    install(Vue) {
        Vue.prototype._getTimeStamp = function () {
            var now = new Date();

            var timestamp = now.getFullYear() + '' + ((now.getMonth() + 1) >= 10 ? (now.getMonth() + 1) : "0" +
                (now.getMonth() + 1)) + (now.getDate() >= 10 ? now.getDate() : "0" + now.getDate()) + (now
                .getHours() >= 10 ? now.getHours() : "0" + now.getHours()) + (now.getMinutes() >= 10 ? now
                .getMinutes() : "0" + now.getMinutes()) + (now.getSeconds() >= 10 ? now.getSeconds() : "0" +
                now.getSeconds());
            return timestamp;
        },
        Vue.prototype.authentication = function (patchl) {

            var path = '/2013-12-26/Accounts/' + this.$store.state.LoginServe.AccountSid + patchl +
                '?sig=' +
                this.$md5(this.$store.state.LoginServe.AppId + this.$store.state.LoginServe.AccountToken +
                    this._getTimeStamp())
            return path
        }, Vue.prototype.authenticationheader = function () {
            let header = {
                Accept: 'application/json;charset=utf-8',
                'Content-Type': 'application/json;charset=utf-8',
                Authorization: this.$Base64.encode(
                    this.$store.state.LoginServe.AppId + ":" + new Date().getTime()
                )
            }
            return header
        },Vue.prototype.IsHost = function (roleId) {
            if(roleId==1||roleId==2||roleId==4||roleId==5||roleId==8){
                return true
            }else{
                return false
            }
        },Vue.prototype.DepartThree = function (list) {
            var map = {}
            list.forEach(function (item) {
                if (!map[item.departId]) {
                    map[item.departId] = item
                }
            })
            list.forEach(function (item) {
                if (item.parentDepartId && item.parentDepartId != 0) {
                    if (map[item.parentDepartId] != undefined) {
                        map[item.parentDepartId].children ? map[item.parentDepartId].children.push(item) : map[item.parentDepartId].children = [item];
                    }
                }
            })
            return list.filter(function (item) {
                return item.parentDepartId === '0'
            })
        }
    }
}


