import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login/loginH5'

/**
 * 重写路由的push,replace方法
 * 解决，相同路由跳转时，报错
 */
const originalPush = Router.prototype.push
const originalReplace = Router.prototype.replace
// push
Router.prototype.push = function push (location, onResolve, onReject) {
  if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
  return originalPush.call(this, location).catch(err => err)
}
// replace
Router.prototype.replace = function push (location, onResolve, onReject) {
  if (onResolve || onReject) return originalReplace.call(this, location, onResolve, onReject)
  return originalReplace.call(this, location).catch(err => err)
}

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/yuntongxun',
      name: 'yuntongxun',
      component: () => import('@/components/yuntongxun/yuntongxun'),
      meta: {requireAuth: true},
      redirect: "/yuntongxun/cloudmeet",
      children: [
        {
          path: '/yuntongxun/cloudmeet',
          name: 'cloudmeet',
          component:  () => import('@/components/meet/cloudMeet'),
        },
        {
          path: '/yuntongxun/meeting',
          name: 'meeting',
          component:  () => import('@/components/meet/Meeting'),
        },
      ]
    },

  ]
})



