import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import getters from './getters'
import state from './state'
import mutations from './mutations'

Vue.use(Vuex)
let LoginServe = {}
if (!!sessionStorage.LoginServe) {
    LoginServe = JSON.parse(sessionStorage.LoginServe)
}
export default new Vuex.Store({
  state: {
    LoginServe: LoginServe,
    ...state
  },
  mutations: {
      SetLoginServe(state, value) {
        console.log('SetLoginServe====', value)
          state.LoginServe = value
      },
      ...mutations
  },
  actions,
  getters,
})



