let timetrans=(timeStamp)=>{
  var date = new Date();
  date.setTime(timeStamp);
  var y = date.getFullYear();
  var m = date.getMonth() + 1;
  m = m < 10 ? ('0' + m) : m;
  var d = date.getDate();
  d = d < 10 ? ('0' + d) : d;
  var h = date.getHours();
  h = h < 10 ? ('0' + h) : h;
  var minute = date.getMinutes();
  var second = date.getSeconds();
  minute = minute < 10 ? ('0' + minute) : minute;
  second = second < 10 ? ('0' + second) : second;
  return  h+':'+minute+':'+second
}

let nameslice=(name)=>{
  if(!name){
    return "";
  }
  if(name.length<=2){
    return name;
  }
  return  name.substring(name.length - 2);
}


let formatDate = (time, fmt) => {
  let date = new Date(Number(time))
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? str : padLeftZero(str))
    }
  }
  return fmt
}
//判断发布时间距离现在的时间
let timestampFormat = (timestamp) => {
  let zeroize = (num) => {
      return (String(num).length == 1 ? '0' : '') + num;
  }
 var hour = new Date(timestamp).getHours();
 //console.log(hour);
  var curTimestamp = parseInt(new Date().getTime()); //当前时间戳
  var timestampDiff = curTimestamp - timestamp; // 参数时间戳与当前时间戳相差秒数

  var curDate = new Date( curTimestamp); // 当前时间日期对象
  var tmDate = new Date( timestamp);  // 参数时间戳转换成的日期对象

  var Y = tmDate.getFullYear(), m = tmDate.getMonth() + 1, d = tmDate.getDate();
  var H = tmDate.getHours(), i = tmDate.getMinutes(), s = tmDate.getSeconds();

  if ( curDate.getFullYear() == Y && curDate.getMonth()+1 == m && curDate.getDate() == d ) {
      return zeroize(H) + ':' + zeroize(i);
  } else {
      var newDate = new Date( (curTimestamp - 86400000)); // 参数中的时间戳加一天转换成的日期对象
      if ( newDate.getFullYear() == Y && newDate.getMonth()+1 == m && newDate.getDate() == d ) {
          return '昨天  ' + zeroize(H) + ':' + zeroize(i);
      } else if ( curDate.getFullYear() == Y ) {
        if (hour < 6) {
          return  zeroize(m) + '月' + zeroize(d) + '日  ' + zeroize(H) + ':' + zeroize(i);
        } else if (hour < 9) {
          return  zeroize(m) + '月' + zeroize(d) + '日  ' + zeroize(H) + ':' + zeroize(i);
        } else if (hour < 12) {
          return  zeroize(m) + '月' + zeroize(d) + '日  ' + zeroize(H) + ':' + zeroize(i);
        } else if (hour < 14) {
          return  zeroize(m) + '月' + zeroize(d) + '日  ' + zeroize(H) + ':' + zeroize(i);
        } else if (hour < 17) {
          return  zeroize(m) + '月' + zeroize(d) + '日  ' + zeroize(H) + ':' + zeroize(i);
        } else if(hour < 19) {
          return  zeroize(m) + '月' + zeroize(d) + '日  ' + zeroize(H) + ':' + zeroize(i);
        } else if(hour < 22) {
          return  zeroize(m) + '月' + zeroize(d) + '日  ' + zeroize(H) + ':' + zeroize(i);
        } else{
          return  zeroize(m) + '月' + zeroize(d) + '日 ' + zeroize(H) + ':' + zeroize(i);
        }
      } else {
          return  Y + '年' + zeroize(m) + '月' + zeroize(d) + '日 ' + zeroize(H) + ':' + zeroize(i);
      }
  }
}
let timestampFormat2 = (timestamp) => {
  let zeroize = (num) => {
    return (String(num).length == 1 ? '0' : '') + num;
  }
  var hour = new Date(timestamp).getHours();
  //console.log(hour);
  var curTimestamp = parseInt(new Date().getTime()); //当前时间戳
  var timestampDiff = curTimestamp - timestamp; // 参数时间戳与当前时间戳相差秒数

  var curDate = new Date( curTimestamp); // 当前时间日期对象
  var tmDate = new Date( timestamp);  // 参数时间戳转换成的日期对象

  var Y = tmDate.getFullYear(), m = tmDate.getMonth() + 1, d = tmDate.getDate();
  var H = tmDate.getHours(), i = tmDate.getMinutes(), s = tmDate.getSeconds();

  if ( curDate.getFullYear() == Y && curDate.getMonth()+1 == m && curDate.getDate() == d ) {
    return zeroize(H) + ':' + zeroize(i);
  } else {
    var newDate = new Date( (curTimestamp - 86400000)); // 参数中的时间戳加一天转换成的日期对象
    if ( newDate.getFullYear() == Y && newDate.getMonth()+1 == m && newDate.getDate() == d ) {
      return '昨天';
    } else if ( curDate.getFullYear() == Y ) {
      if (timestampDiff > 24*60*60*1000*2 && timestampDiff < 24*60*60*1000*7){
        var week;
        if(tmDate.getDay() == 0) week = "星期日"
        if(tmDate.getDay() == 1) week = "星期一"
        if(tmDate.getDay() == 2) week = "星期二"
        if(tmDate.getDay() == 3) week = "星期三"
        if(tmDate.getDay() == 4) week = "星期四"
        if(tmDate.getDay() == 5) week = "星期五"
        if(tmDate.getDay() == 6) week = "星期六"
        return week;
      }else {
        return  zeroize(m) + '/' + zeroize(d);
      }
    } else {
      return  Y + '/' + zeroize(m) + '/' + zeroize(d);
    }
  }
}
let padLeftZero = (str) => {
  return ('00' + str).substr(str.length)
}

// 音频换算时间
let secondsTrans = (time) => {
  let duration = Number(parseInt(time/650))
  if(duration > 60) duration = 60
  else if(duration < 1) duration = 1
  return duration
  // return Math.round(time*1024/768000)//取整
}
//计算时间间隔
let timeIntervalFun = (usedTime) => {
  let days = Math.floor(usedTime / (24 * 3600 * 1000)); // 计算出天数
  let leavel = usedTime % (24 * 3600 * 1000); // 计算天数后剩余的时间
  let hours = Math.floor(leavel / (3600 * 1000)); // 计算剩余的小时数
  let leavel2 = leavel % (3600 * 1000); // 计算剩余小时后剩余的毫秒数
  let minutes = Math.floor(leavel2 / (60 * 1000)); // 计算剩余的分钟数
  let leavel3 = leavel2 % (60 * 1000); // 计算剩余分钟后剩余的毫秒数
  let seconds = Math.floor(leavel3 / (1000)); // 计算剩余的分钟数
  return (days?(days + '天'):'') + (hours?hours + '时':'') + (minutes?minutes + '分':'') + (seconds?seconds + '秒':'');
}
// 文件大小换算
let fileSzieTran = (fileByte) => {
  if(!fileByte) return "0KB"
  const fileSizeByte = fileByte;
  let fileSizeMsg = "";
  if (fileSizeByte < 1024) fileSizeMsg = fileSizeByte + "B"
  else if (fileSizeByte >= 1024 && fileSizeByte < 1048576) fileSizeMsg = (fileSizeByte / 1024).toFixed(1) + "KB";
  else if (fileSizeByte == 1048576) fileSizeMsg = "1MB";
  else if (fileSizeByte > 1048576 && fileSizeByte < 1073741824) fileSizeMsg = (fileSizeByte / (1024 * 1024)).toFixed(1) + "MB";
  else if (fileSizeByte > 1048576 && fileSizeByte == 1073741824) fileSizeMsg = "1GB";
  else if (fileSizeByte > 1073741824 && fileSizeByte < 1099511627776) fileSizeMsg = (fileSizeByte / (1024 * 1024 * 1024)).toFixed(1) + "GB";
  else if (fileSizeByte > 1099511627776) fileSizeMsg = "文件超过1TB";
  return fileSizeMsg;
}

let getDayName = (data) => {
  let td = new Date()
  td = new Date(td.getFullYear(), td.getMonth(), td.getDate())
  let od = new Date(Number(data))
  od = new Date(od.getFullYear(), od.getMonth(), od.getDate())
  let xc = (od-td)/1000/60/60/24
  let str = formatDate(data, 'hh:mm')
  if(xc == -1){//昨天
    return '昨天 ' + str
  }else if(xc == 0){//今天
    return '今天 ' + str;
  }else{//其他时间
    return formatDate(data, 'MM-dd hh:mm')
  }
}
