/*容信工具方法封装
* getAudioSoundUrl 获取录音文件网络播放路径
* computeAudioDuration 根据录音文件大小计算时长
* analyticBuilderDomain 解析消息体msgDomain属性
* */
import store from '@/store'
import util from './util'
import pinyin from 'js-pinyin'
import CryptoJS from 'crypto-js'

let RL_YTX = window.RL_YTX
let RX_Util = {
  delay : (function () {
    let timer = 0
    return function (callback, ms) {
      clearTimeout(timer)
      timer = setTimeout(callback, ms)
    }
  })(),
  // 获取录音文件网络播放路径
  getAudioSoundUrl (fileUrl) {
    return new Promise((resolve, reject) => {
      RL_YTX.getAudioSource(fileUrl, res => {
        resolve(res)
      }, err => {
        reject(err)
      })
    })
  },
  // 根据录音文件大小计算时长
  computeAudioDuration (size) {
    let duration = Number(parseInt(size / 650))
    if (duration > 60) {
      duration = 60
    } else if (duration < 1) duration = 1
    return duration
  },
  // 解析消息体msgDomain属性
  analyticBuilderDomain (domainStr) {
    let _this = this
    if (typeof domainStr === 'object') {
      return domainStr
    }
    let domain = null, exception = '', firstPart
    if (domainStr && domainStr.length) {
      if (/^customtype/.test(domainStr)) {
        firstPart = domainStr.substr(0, domainStr.indexOf(','))
        domainStr = domainStr.substr(domainStr.indexOf(',') + 1)
        if(firstPart) firstPart = _this.parseUserDataToJson(firstPart)
      }
      if (!/{|}|userdata/i.test(domainStr)){
        try {
          let decodeStr = Base64.decode(domainStr);
          domainStr = decodeStr;
        } catch (e) {
          console.log('msgDomain解析失败:%s', domainStr, exception)
          domainStr = domainStr;
        }
      }
      domainStr = domainStr.replace(/\n|\t/g, '')
      if (domainStr.indexOf('UserData') !== -1) {
        domainStr = domainStr.split('UserData=')[1]
        // domain = _this.parseUserDataToJson(domainStr)
        domain = !!domainStr ? _this.parseUserDataToJson(domainStr) :  domainStr
      } else {
        try {
          if(domainStr) domain = JSON.parse(domainStr)
        } catch (e) {
          exception = e
          domain = domainStr
        }
      }
      if (typeof domain === 'object') {
        if (firstPart) {
          domain = {...domain, ...firstPart}
        }
        console.log('msgDomain解析成功:', domainStr)
      } else {
        console.log('msgDomain解析失败:%s', domainStr, exception)
      }
    }
    return domain ? domain : domainStr
  },
  parseUserDataToJson (userData) {
    userData = userData.replace(/\{|\}|\"/g, '')
    let userDataArr = userData.split(';')
    userData = {}
    userDataArr.map(function (data) {
      if (/\=/.test(data)) {
        data = data.split('=')
        userData[data[0]] = data[1] || ''
      }
    })
    return userData
  },
  // 根据首字母排序并分类
  dataLetterSort (data, field) {
    let letter_reg = /^[A-Z]$/
    let list = new Array()
    let letter = ''
    for (let i = 0; i < data.length; i++) {
      // 添加 # 分组，用来 存放 首字母不能 转为 大写英文的 数据
      // list['#'] = new Array()
      // 首字母 转 大写英文
      letter = data[i] && data[i][field]? data[i][field]: pinyin.getCamelChars(data[i].username)
      letter = letter.substr(0, 1).toUpperCase()
      // 是否 大写 英文 字母
      if (!letter_reg.test(letter)) {
        letter = '#'
      }
      // 创建 字母 分组
      if (!(letter in list)) {
        list[letter] = new Array()
      }
      // 字母 分组 添加 数据
      list[letter].push(data[i])
    }
    // 转换 格式 进行 排序；
    let result = new Array()
    for (let key in list) {
      result.push({
        letter: key,
        list: list[key]
      })
    }
    result.sort(function (x, y) {
      return x.letter.charCodeAt(0) - y.letter.charCodeAt(0)
    })
    // # 号分组 放最后
    if(result[0] && result[0].key == '#'){
      let last_arr = result[0]
      result.splice(0, 1)
      result.push(last_arr)
    }

    // 转换 数据 格式
    let json_sort = {}
    for (let i = 0; i < result.length; i++) {
      json_sort[result[i].letter] = result[i].list
    }
    return json_sort
  },
  // 消息显示时间格式化
  formatMsgShowTime (time, isShowInContactList = false) {
    let showStr = ''
    time = time || new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0)
    if (time) {
      const now = new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0)),
      yesterday = new Date(new Date(new Date(new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0)).toLocaleDateString()).getTime() - 24 * 60 * 60 * 1000).getTime();
      time = new Date(time);
      console.log('formatMsgShowTime',time)
      const timeDiff = Math.floor((now.getTime() - time.getTime()) / 1000 / 24 / 60 / 60);
      if(timeDiff == -1){
        showStr = util.time2StringNotSec(time)
      }else if (timeDiff == 0) {
        showStr = now.getDay() == time.getDay() ? util.generateTimeString(time).substr(11, 5) : rxVue.$t('last.yesterday')
      } else if (timeDiff == 1) {
        showStr = time.getTime() < yesterday ? rxVue.$t('last.beforeYesterday') : rxVue.$t('last.yesterday');
      } else if (timeDiff >= 2 && timeDiff < 7) {
        showStr = rxVue.$t('last.beforeYesterday')
        let week = [rxVue.$t('last.sunday'), rxVue.$t('last.monday'), rxVue.$t('last.tuesday'), rxVue.$t('last.wednesday'), rxVue.$t('last.thursday'), rxVue.$t('last.friday'), rxVue.$t('last.saturday')]
        return week[time.getDay()] + ' ' + util.generateTimeString(time).substr(11, 5)
      } else {
        showStr = util.generateTimeString(time).substr(0, isShowInContactList ? 10 : 16)
      }
      if (!isShowInContactList && /rxVue.$t('last.yesterday')|rxVue.$t('last.beforeYesterday')/.test(showStr)) {
        showStr += ' ' + util.generateTimeString(time).substr(11, 5)
      }
    }
    return showStr
  },
  // 判断字符串中是否含有表情字符
  hasEmoJiCharacter (str) {
    if (str && typeof(str) == 'string') {
      for (let i = 0; i < str.length; i++) {
        let hs = str.charCodeAt(i)
        if (0xd800 <= hs && hs <= 0xdbff) {
          if (str.length > 1) {
            let ls = str.charCodeAt(i + 1)
            let uc = ((hs - 0xd800) * 0x400) + (ls - 0xdc00) + 0x10000
            if (0x1d000 <= uc && uc <= 0x1f77f) {
              return true
            }
          }
        } else if (str.length > 1) {
          let ls = str.charCodeAt(i + 1)
          if (ls == 0x20e3 || ls == 65039) {
            return true
          }
        } else {
          if (0x2100 <= hs && hs <= 0x27ff) {
            return true
          } else if (0x2B05 <= hs && hs <= 0x2b07) {
            return true
          } else if (0x2934 <= hs && hs <= 0x2935) {
            return true
          } else if (0x3297 <= hs && hs <= 0x3299) {
            return true
          } else if (hs == 0xa9 || hs == 0xae || hs == 0x303d || hs == 0x3030 || hs == 0x2b55 || hs == 0x2b1c || hs == 0x2b1b || hs == 0x2b50) {
            return true
          }
        }
      }
    }
  },
  // 表情转换
  transformEmoJiCharacter (emoJiStr, size = 20) {
    for (let i in emoji.show_dataAuto) {
      let item = emoji.show_dataAuto[i]
      let imgStr = '<img class="emoji" src="./static/img-apple-64/' + item.unicode + '.png" height="' + size + '" width="' + size + '" style="vertical-align: middle;" align="absmiddle">'
      let reg = new RegExp('\\' + item.icon, 'g')
      emoJiStr = emoJiStr.replace(reg, imgStr)
    }
    return emoJiStr
  },
  // 图片转换为表情
  transformImgToEmoji(img){
    let unicode = img.split('/static/img-apple-64/')[1].replace(/.png/g,"")
    console.log('transformImgToEmoji', unicode)
    for (let i in emoji.show_dataAuto) {
      if(emoji.show_dataAuto[i].unicode == unicode){
        img = emoji.show_dataAuto[i].icon
      }
    }
    return img
  },
  formatMsgContentHref (str) {
    // let reg = /(https?:\/\/)([0-9a-z.]+)(:[0-9]+)?([/0-9a-z._]+)?(\?[0-9a-z_&=]+)?(#[0-9-a-z_]+)?/i
    let reg = /(https?|ftp|file)(:\/\/)[-A-Za-z0-9+&@#/%?=~_|!:,.;]+[-A-Za-z0-9+&@#/%=~_|]?/i
    let repSign = {}, count = 0
    let rep = function (s) {
      let result = reg.exec(s)
      if (result && result[0]) {
        count++
        let sign = '$' + count
        repSign[sign] = result[0]
        s = s.replace(result[0], sign)
        return rep(s)
      } else {
        return s
      }
    }
    str = rep(str)
    for (let i in repSign) {
      if(/confId/.test(repSign[i])){
        str = str.replace(i, '<a target="_self" href="' + repSign[i] + '" class="linkColor">' + repSign[i] + '</a>')
      }else{
        str = str.replace(i, '<a target="_blank" href="' + repSign[i] + '" class="linkColor">' + repSign[i] + '</a>')
      }
    }
    return str
  },
  // 自定义消息文本
  formatMsgContent (msg) {
    let _this = this
    if (!msg || typeof msg !== 'object' || !msg.msgType) {
      console.warn('Invalid message:', msg)
      return msg
    }
    // console.log('formatMsgContent', msg)
    if (msg.msgContent) {
      // 表情转换
      if (_this.hasEmoJiCharacter(msg.msgContent)) {
        msg.emoji = _this.transformEmoJiCharacter(msg.msgContent)
      }
      // return msg
    }
    if (msg.msgContent && /http:\/\/|https:\/\//.test(msg.msgContent)) {
      msg.msgHrefContent = _this.formatMsgContentHref(msg.msgContent)
    }
    let msgDomain = this.analyticBuilderDomain(msg.msgDomain)
    switch (Number(msg.msgType)) {
      case 0 || 1 || 9:
        break
      case 2:
        msg.lastTitle = `[${rxVue.$t('message.audio')}]`;
        break
      case 3:
        msg.lastTitle = `[${rxVue.$t('message.video')}]`;
        break
      case 4:
        if (msg.picAndTxt || (msgDomain && msgDomain.sMsgType == '11')) {
          msg.lastTitle = msg.msgContent.replace(/<br\/>/g, '\n')
        } else {
          msg.lastTitle = `[${rxVue.$t('connect.picture')}]`;
        }
        break
      case 5:
        msg.lastTitle = `[${rxVue.$t('last.position')}]`;
        break
      case 6:
      case 7:
        msg.lastTitle = `[${rxVue.$t('connect.file')}]`;
        // 合并转发另行判断
        if (msgDomain && (msgDomain.customtype || msgDomain.sMsgType == '14')) {
          msg.lastTitle = `[${rxVue.$t('connect.chatRecord')}]`;
        }
        break
      case 8:
        msg.lastTitle = `[${rxVue.$t('connect.link')}]`;
        break
      case 11:
        if (typeof msg.msgContent === "string") {
          msg.lastTitle = msg.msgContent.replace(/<br\/>/g, '\n')
        }
        break
      case 21:
        msg.lastTitle = `[${rxVue.$t('last.msgFired')}]`;
        break
      case 25:
        if (msgDomain) {
          msg.lastTitle = (msgDomain.deviceno ? rxVue.$t('last.msgBack_I')  :  rxVue.$t('last.msgBack_O')) + rxVue.$t('last.msgBack')
        }
        break
      case 26:
        if (msgDomain && msgDomain.WBSS_SHOWMSG) {
          msg.lastTitle = `[${rxVue.$t('last.wbShare')}]`;
        }
        // 好友添加
        if ((msgDomain.sMsgType == "25" && msgDomain.status == "2") || (msgDomain.friendPBSIM && msgDomain.friendPBSIM.addFriend && msgDomain.friendPBSIM.addFriend.agreeStatus == "1")) {
          let msgSenderName = ""
          if(msg.talker == msg.msgSender){
            msgSenderName = msg.senderNickName || msg.msgSenderNick
            msg.lastTitle = rxVue.$t('last.add_I') +msgSenderName+ rxVue.$t('last.beeFriend');
            msg.msgContent = rxVue.$t('last.add_I') +msgSenderName+ rxVue.$t('last.beeFriend');
          }
        }
        break
      default:
    }
    if(msg.operationType == 2){
      msg.lastTitle = '"' + (msg.senderNickName || rxVue.$t('last.msgBack_O')) + '" ' + rxVue.$t('last.msgBack')
    }else if(msg.operationType == 1){
      msg.lastTitle = rxVue.$t('last.msgBack_I') + rxVue.$t('last.msgBack')
    }
    return msg
  },
  // 格式化本地消息存储
  formatMsg (msg, callback) {
    let _this = this
    // 完善消息体字段信息
    if (!msg.msgId && msg.msgClientNo) {
      msg.msgId = msg.msgClientNo
    }
    // 文件数据拼接文件地址
    if ((msg.msgType == 2 || msg.msgType == 4 || msg.msgType == 6 || msg.msgType == 7 || msg.msgType == 8) && msg.msgFileUrl && !/http/.test(msg.msgFileUrl)) {
      msg.fileFullPath = 'http://' + store.state.user_info.lvs[0] + msg.msgFileUrl
    }
    msg.msgDateCreated = new Date(msg.msgDateCreated).getTime()
    if (msg.msgContent) {//解码msgContent
      msg.msgContent = Base64.decode(msg.msgContent)
    }
    if (msg.msgFileName) {
      msg.msgFileName = Base64.decode(msg.msgFileName)
    }
    if (msg.needFired) {
      msg.msgContent = store.state.user_account == msg.msgSender ?  `[${rxVue.$t('connect.privateMessageSend')}]` : `[${rxVue.$t('connect.privateMessageReceived')}]`
    }
    msg = _this.formatMsgContent(msg)
    if (msg.msgDomain && msg.msgDomain.length) {//解码msgDomain
      msg.msgDomain = _this.analyticBuilderDomain(msg.msgDomain) // 测试解析函数
    }
    if (typeof callback === 'function') {
      callback(msg)
    } else {
      return msg
    }
  },
  // 消息builder与sdk返回数据合并
  combineMsgBuilderAndRes (builder, resData) {
    let _this = this
    /*console.log('builder:', {...builder})
    console.log('resData:', {...resData})*/
    if (!resData.msgId && resData.msgClientNo) {
      resData.msgId = resData.msgClientNo
    }
    let msg = {
      msgSender: store.state.user_account,
      msgReceiver: builder.receiver,
      msgType: builder.msgType,
      msgDomain: builder.getDomain(),
      msgContent: builder.getText() || '',
      ...resData
    }
    if (resData.fileUrl) {
      msg.msgFileUrl = resData.fileUrl
      if (/^http/.test(resData.fileUrl)) {
        msg.fileFullPath = resData.fileUrl
      }
    }
    if (builder._file) {
      msg.msgFileName = builder._file.name
      msg.msgFileSize = builder._file.size
    }
    if (msg.msgDomain && typeof msg.msgDomain === 'string') {
      try {
        msg.msgDomain = eval('(' + msg.msgDomain + ')')
      } catch (e) {
        console.log('eval msg.msgDomain fail:', msg.msgDomain)
      }
    }
    msg = _this.formatMsgContent(msg)
    // console.log('returnMsg:', msg)
    return msg
  },
  getType (obj) {
    if (Object.prototype.toString.call(obj) == '[object Object]') {
      return 'Object'
    } else if (Object.prototype.toString.call(obj) == '[object Array]') {
      return 'Array'
    } else {
      return 'nomal'
    }
  },
  deepCopy (obj) {
    if (this.getType(obj) == 'nomal') {
      return obj
    } else {
      let newObj = this.getType(obj) == 'Object' ? {} : []
      for (let key in obj) {
        if (obj.hasOwnProperty(key)) {
          newObj[key] = this.deepCopy(obj[key])
        }
      }
      return newObj
    }
  },
  // 判断pbs请求是否成功
  checkRequestSuccess (reqRes) {
    return reqRes.Response && reqRes.Response.head && reqRes.Response.head.statusCode === '000000'
  },
  // 封装收藏结构体
  formatCollectObj (msg) {
    console.log('formatCollectObj:', msg)
    let collect = {
      fromAccount: msg.msgSender,
      url: ''
    }
    if (!!msg.favoriteMsgId){
      collect.favoriteMsgId = msg.favoriteMsgId;
    } else if (msg.msgId || msg.msgClientNo) {
      collect.favoriteMsgId = hex_md5(msg.msgId || msg.msgClientNo)
    }else{
      collect.favoriteMsgId = new Date().getTime() + (parseInt(store.state.user_info.timeDifference) || 0)
    }
    // 收藏类型 1,文本 ；2，图片；3，网页；4，语音；5，视频；6，图文；7，附件 8，位置；
    switch (parseInt(msg.msgType)) {
      case 1:
        if (/^http/.test(msg.msgContent) && msg.webTitle) {
          collect.type = 3
          collect.txtContent = {
            url: msg.msgContent,
            urlTitle: msg.webTitle,
            urlDescription: msg.webDesc,
            urlThum: msg.webImagePath
          }
        } else {
          collect.type = 1
          collect.txtContent = {content: msg.msgContent}
        }
        break
      case 2:
        collect.type = 4
        collect.txtContent = {
          url: /^http/.test(msg.msgFileUrl) ? msg.msgFileUrl : msg.fileFullPath,
          fileSize: msg.msgFileSize,
          fileName: msg.msgFileName,
          userData: msg.msgDomain,
          fileExt: msg.msgFileName&&msg.msgFileName.substr(msg.msgFileName.lastIndexOf('.') + 1) || 'amr',
          length: msg.duration + ''
        }
        break
      case 3:
        collect.type = 5
        collect.txtContent = {
          url: /^http/.test(msg.msgFileUrl) ? msg.msgFileUrl : msg.fileFullPath,
          fileSize: msg.msgFileSize,
          fileName: msg.msgFileName,
          userData: msg.msgDomain,
          fileExt: msg.msgFileName&&msg.msgFileName.substr(msg.msgFileName.lastIndexOf('.') + 1),
        }
        break
      case 4:
        collect.type = 2
        collect.txtContent = {
          url: /^http/.test(msg.msgFileUrl) ? msg.msgFileUrl : msg.fileFullPath,
          fileSize: msg.msgFileSize,
          fileName: msg.msgFileName,
          userData: msg.msgDomain,
          fileExt: msg.msgFileName&&msg.msgFileName.substr(msg.msgFileName.lastIndexOf('.') + 1)
        }
        if(msg.msgDomain && msg.msgDomain.sMsgType == '11'){
          // 图文
          collect.type = 6
          collect.txtContent.content = msg.msgContent
        }
        break
      case 5:
        collect.type = 8
        collect.txtContent = {
          content: msg.msgContent.title,
          latitude: msg.msgContent.lat,
          longitude: msg.msgContent.lon
        }
        break
      case 6:
      case 7:
        collect.type = 7
        collect.txtContent = {
          url: /^http/.test(msg.msgFileUrl) ? msg.msgFileUrl : msg.fileFullPath,
          fileSize: msg.msgFileSize,
          fileName: msg.msgFileName,
          userData: msg.msgDomain,
          fileExt: msg.msgFileName.substr(msg.msgFileName.lastIndexOf('.') + 1),
          originType: msg.msgType
        }
        break
      case 8:
        // if (/^http/.test(msg.msgContent) && msg.webTitle) {
          collect.type = 3
          collect.txtContent = {
            url: msg.msgContent,
            urlTitle: msg.webTitle,
            urlDescription: msg.webDesc,
            urlThum: msg.webImagePath
          }
        // }
        break
      default:
      // 文章收藏在服务号里单独处理
      // 视频、图文收藏暂未支持
    }
    if (msg.msgFileUrl && /^http/.test(msg.msgFileUrl) && !msg.expandContent) {
      collect.txtContent.url = collect.url = msg.msgFileUrl.replace(/(.*)_thum/,"$1")
    } else if (msg.fileFullPath && /^http/.test(msg.fileFullPath) && !msg.expandContent) {
      collect.txtContent.url = collect.url = msg.fileFullPath.replace(/(.*)_thum/,"$1")
    }
    if (collect.txtContent && typeof collect.txtContent === 'object' && typeof collect.txtContent.userData === 'object') {
      collect.txtContent.userData = JSON.stringify(collect.txtContent.userData)
    }
    if (collect.txtContent && typeof collect.txtContent === 'object') {
      collect.txtContent = JSON.stringify(collect.txtContent)
    }
    console.log('collect-------', collect)
    return collect
  },
  getTalker (obj, user_account) {
    let talker = ''
    if (obj.msgReceiver) {
      if (/^g/.test(obj.msgReceiver) || obj.msgReceiver != user_account) {
        // 接收方为群组或非自己，则接收者为联系人
        talker = obj.msgReceiver
      } else {
        talker = obj.msgSender
      }
    } else {
      talker = obj.account == user_account ? '~ytxfa' : obj.account
    }
    return talker
  },
  /*
  *  校验用户等级权限
  * targetLevel 目标用户的用户等级
  * */
  checkLevelPermission (targetLevel = -1) {
    // targetLevel = 1
    let myAccount = store.state.user_info.account, myLevel = store.state.user_info.personLevel || -1
    let levelPermission = store.state.levelPermission || {}, dataVisible = true, chatAble = true
    // console.log(levelPermission, myLevel, targetLevel)
    if (!myLevel || myLevel == -1 || !targetLevel || targetLevel == -1 || /-1|null/.test(targetLevel + '')) {
      // 无权限限制
    } else if (levelPermission['level_' + targetLevel]) {
      if (levelPermission['level_' + targetLevel]['limitDataVisible_' + myLevel]) {
        dataVisible = false
      }
      if (levelPermission['level_' + targetLevel]['limitChatAble_' + myLevel]) {
        chatAble = false
      }
      if (levelPermission['level_' + targetLevel].whiteList.indexOf(myAccount) !== -1) {
        dataVisible = true
        chatAble = true
      }
    }
    return {
      dataVisible,
      chatAble
    }
  },
  // 补充用户权限判断
  supplementLevelPermission (user) {
    // console.log("supplementLevelPermission:", user)
    let _this = this
    user = _this.formatePhotourl(user)
    // 判断是否为群组或自己或好友关系
    if (/^g|~ytxfa|ServiceNum/.test(user.account) || user.account == store.state.user_account || store.state.friendAccounts.indexOf(user.account) !== -1){
      user.dataVisible = true;
      user.chatAble = true;
      return user;
    }
    // 判断是否已在联系人列表中
    let connectors = store.state.connectors;
    for (let i = 0; i < connectors.length; i++) {
      if (connectors[i].account == user.account){
        user.dataVisible = true;
        user.chatAble = true;
        return user;
      }
    }
    // 判断用户等级权限
    let checkPermissionRet = _this.checkLevelPermission(user.personLevel)
    user.dataVisible = checkPermissionRet.dataVisible
    user.chatAble = checkPermissionRet.chatAble
    if (!user.dataVisible) {
      user.tel = '******'
      user.email = '******'
    }
    return user
  },
  // 切换服务配置
  setServerConfigs(obj, httpProtocol = process.env.SERVER_PROTOCOL) {
    obj = obj || {}
    let address = obj.address || process.env.SERVER_ADDRESS, port = obj.port || process.env.SERVER_PORT
    port = port - 0;
    let httpPort, httpsPort, mWsgPort, wsProtocol = "ws";
    let domainIsEncrypted = /^https/.test(window.location.href);
    if (httpProtocol == "https") {
      httpsPort = port;
      httpPort = port - 1;
      wsProtocol += "s";
    } else {
      httpPort = port;
      httpsPort = port + 1;
    }
    mWsgPort = httpsPort + 1;
    httpProtocol = /:/.test(httpProtocol) ? httpProtocol : httpProtocol + '://';
    wsProtocol = /:/.test(wsProtocol) ? wsProtocol : wsProtocol + '://'
    let whiteAddress = obj.whiteAddress || process.env.WHITEBOARD_SERVER, whitePort = obj.whitePort || '8001'
    /*PBS_BASE_URL pbs服务
    REST_BASE_URL rest服务
    PNF_BASE_URL 服务号服务
    FILE_UPLOAD_URL 文件上传
    FILE_ACCESS_URL 文件访问
    WS_SERVER_URL sdkws
    MWS_G_SERVER_URL 媒体ws*/
    let serverConfigs = {
      PBS_BASE_URL: `${httpProtocol}${address}:${/https/.test(httpProtocol) ? httpsPort : httpPort}`,
      REST_BASE_URL: `${httpProtocol}${address}:${/https/.test(httpProtocol) ? httpsPort : httpPort}`,
      PNF_BASE_URL: `${httpProtocol}${address}:${/https/.test(httpProtocol) ? httpsPort : httpPort}`,
      FILE_UPLOAD_URL: `${httpProtocol}${address}:${/https/.test(httpProtocol) ? httpsPort : httpPort}/uploadFile`,
      FILE_ACCESS_URL: `${httpProtocol}${address}:${/https/.test(httpProtocol) ? httpsPort : httpPort}/accessFile`,
      FILE_DOWN_URL: `${domainIsEncrypted ? 'https' : 'https'}://${address}:${domainIsEncrypted ? httpsPort : httpsPort}/accessFile/`,
      WS_SERVER_URL: `${wsProtocol}${address}:${/wss/.test(wsProtocol) ? httpsPort : httpPort}/ws`,
      MWS_G_SERVER_URL: `${wsProtocol}${address}:${mWsgPort}/h5websocket`,
      WHITEBOARD_SERVER: `${whiteAddress}:${whitePort}`,
      // MWS_G_SERVER_URL:"wss://192.168.27.239:1443/h5websocket"
      DVR_FILE_URL:`${domainIsEncrypted ? 'https' : 'https'}://${address}:${domainIsEncrypted ? httpsPort : httpsPort}/dvrFile/`,
    }
    LS.set("ServerConfigs", serverConfigs)
    window.ServerConfigs = serverConfigs;
    return serverConfigs;
  },
  // 获取服务配置
  getServerConfigs () {
    let _this = this, serverConfigs = LS.get('ServerConfigs');
    if (!serverConfigs) {
      let href = window.location.href, httpProtocol = /https:/.test(href) ? "https" : "http",
        domain = href.replace(/https?\:\/\//i, '').split("/")[0].split(":");
        let obj = {
          address: domain[0],
          port: /*domain[1] || */(httpProtocol == "http" ? "9001" : "9002"),
        }
      // let address = domain[0], port = /*domain[1] || */(httpProtocol == "http" ? "9001" : "9002");
      // 开发调试时默认使用配置的域名，其他环境则使用域名默认
      serverConfigs = obj.address === "localhost" ? _this.setServerConfigs() : _this.setServerConfigs(obj, httpProtocol);
    }
    return serverConfigs && typeof serverConfigs === "string" ? JSON.parse(serverConfigs) : serverConfigs;
  },
  // 补充文件地址域名
  prefixFileUrl(url){
    return !!url && !/^http|^blob/.test(url) ? window.ServerConfigs.FILE_ACCESS_URL + url : url
  },
  // 注册vue bus事件
  registerBusEvents (events, vm) {
    for (let name in events) {
      vm.$bus.off(name)
      vm.$bus.on(name, events[name])
    }
  },
  // for循环队列
  queue(list, max, callback){
    let listObj = {};
    let times = Math.ceil(list.length / max);
    let currentIndex = 0;
    for(let i = 0;i<times;i++){
        listObj[i] = list.slice(i * max,(i + 1) * max);
    }
    // console.log(times,'times-------------')
    // console.log(listObj,'listObj-----------')
    this.sendRequest(listObj, currentIndex, max, callback)
  },
  sendRequest(listObj, currentIndex, max, callback){
    if(listObj[currentIndex]){
      listObj[currentIndex].forEach((v,i)=>{
        callback && callback(v)
        if(i == (max-1)){
          currentIndex ++
          console.log('开始下一批')
          this.sendRequest(listObj, currentIndex, max, callback)
        }
      })
    }
  },
  dataHandling(arr,num) {
    let newArr = []
    // 每页显示 num 条
    let arrLength = arr.length; // 数组长度
    let index = 0;
    for (let i = 0; i < arrLength; i++) {
         if (i % num === 0 && i !== 0) { // 可以被 10 整除
              newArr.push(arr.slice(index, i));
              index = i;
          };
          if ((i + 1) === arrLength) {
               newArr.push(arr.slice(index, (i + 1)));
          }
    };
    return newArr
  },
  encrypt(data, keyStr, ivStr) {
    if(!ivStr) ivStr = '0000000000000000'
    var sendData = CryptoJS.enc.Utf8.parse(data);
    var key = CryptoJS.enc.Utf8.parse(keyStr);
    var iv  = CryptoJS.enc.Utf8.parse(ivStr);
    var encrypted = CryptoJS.AES.encrypt(sendData, key,{iv:iv,mode:CryptoJS.mode.CBC,padding:CryptoJS.pad.Pkcs7});
    //return CryptoJS.enc.Base64.stringify(encrypted.toString(CryptoJS.enc.Utf8));
    return CryptoJS.enc.Base64.stringify(encrypted.ciphertext);
  },
  //解密
  decrypt(data, keyStr, ivStr) {
    if(!ivStr) ivStr = '00000000'
    var key = CryptoJS.enc.Utf8.parse(keyStr);
    var iv  = CryptoJS.enc.Utf8.parse(ivStr);
    //解密的是基于BASE64的数据，此处data是BASE64数据
    var decrypted = CryptoJS.AES.decrypt(data, key, {iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7});
    return decrypted.toString(CryptoJS.enc.Utf8);
  },
  encryptedHttp(){
    let flag = false
    let domainIsEncrypted = /^https/.test(window.location.protocol);
    // 判断是否是:8888结尾
    if(domainIsEncrypted && window.CurrentUser && window.CurrentUser.confMap && window.CurrentUser.confMap['clientAuthResp_fileServer_Url']){
      flag = true
    }
    return flag
  },
  formatePhotourl(data, str){
    // let flag = this.encryptedHttp()
    // if(!flag){//当前环境的协议为http
      str = str || 'photourl'
      if(typeof data === 'object' && data instanceof Array){
        let result = []
        data.length && data.forEach(item=>{
          if (item[str]) {
            item[str] = this.replaceStr(item[str])
          }
          result.push(item)
        })
        return result
      }else{
        if (data && data[str]) {
          data[str] = this.replaceStr(data[str])
        }
        return data
      }
    // }
  },
  replaceStr(data){
    if(/_sd_thum$/.test(data)) data = data.replace(/(.*)_sd_thum/,"$1_thum")
    data = data.replace(/\&amp;/g, '&')
    let localFileUrl = window.location.hostname + ':8888'
    let fileServerHttp = window.CurrentUser.lvs?window.CurrentUser.lvs[0]:(window.CurrentUser.LvsAddrs ? window.CurrentUser.LvsAddrs[0] : "")
    let fileServerHttps = window.ServerConfigs.FILE_ACCESS_URL//window.CurrentUser.confMap['clientAuthResp_fileServer_Url']
    if(data.indexOf(localFileUrl) > -1){//替换本地8888服务地址
      data = data.replace('http://'+ localFileUrl, fileServerHttps)
    }else if(data.indexOf(fileServerHttp) > -1){//替换lvs8888服务地址
      data = data.replace('http://'+ fileServerHttp, fileServerHttps)
    }else if(window.CurrentUser.confMap && data.indexOf(window.CurrentUser.confMap['clientAuthResp_fileServer_Url']) > -1){
      data = data.replace(window.CurrentUser.confMap['clientAuthResp_fileServer_Url'], fileServerHttps)
    }else{
      if (/^http:/.test(data) && /:8888/.test(data)){
        let str = data.split(':8888')[0]+':8888'
        data = data.replace(str, fileServerHttps)
      }
    }
    // console.log('data======',data)
    return data
  },
  fun_date(num, time) {
    var date1 = time ? new Date(time) : new Date();
    var date2 = new Date(date1);
    date2.setDate(date1.getDate() + num);
    //num是正数表示之后的时间，num负数表示之前的时间，0表示今天
    var time2 = (date2.getMonth() + 1) + "月" + date2.getDate()+ '日';
    console.log(time2);
    return time2;
  },
  // 服务号详情地址替换
  serviceNumberUrlReplace(url){
    if((url.indexOf("/pn/vMsgContent/") != -1 || url.indexOf("/pn/msgComment/vComPage/" ) != -1 || url.indexOf("/pn/vMenuMsgContent/") != -1 ||url.indexOf("/pn/vMsgContentMenu/") != -1 ) && url.indexOf("notsent") == -1){
      let indexOf = url.lastIndexOf("/");
      url = url.substring(0, indexOf + 1);
      url = url + store.state.user_account +"?vType=H5" ;
    }
    return url
  },
  checkUserStatus(status){
    let flag = false
    if(status == 3){
      rxVue.$message.warning('该用户已离职');
      flag = true
    }else if(status == 4){
      rxVue.$message.warning(rxVue.$t('last.personFreeze'));
      flag = true
    }
    return flag
  }
}
export default {...RX_Util}



