import Vue from 'vue'
let watermarkContent = {}
// v-dialogDrag: 弹窗拖拽属性
Vue.directive('dialogDrag', {
    bind(el, binding, vnode, oldVnode) {
        const dialogHeaderEl = el.querySelector('.el-dialog__header');
        const dragDom = el.querySelector('.el-dialog');
        //dialogHeaderEl.style.cursor = 'move';
        dialogHeaderEl.style.cssText += ';cursor:move;'
        dragDom.style.cssText += ';top:0px;'

        // 获取原有属性 ie dom元素.currentStyle 火狐谷歌 window.getComputedStyle(dom元素, null);
        const sty = (function () {
            if (window.document.currentStyle) {
                return (dom, attr) => dom.currentStyle[attr];
            } else {
                return (dom, attr) => getComputedStyle(dom, false)[attr];
            }
        })()

        dialogHeaderEl.onmousedown = (e) => {
            // 鼠标按下，计算当前元素距离可视区的距离
            const disX = e.clientX - dialogHeaderEl.offsetLeft;
            const disY = e.clientY - dialogHeaderEl.offsetTop;

            const screenWidth = document.body.clientWidth; // body当前宽度
            const screenHeight = document.documentElement.clientHeight; // 可见区域高度(应为body高度，可某些环境下无法获取) 

            const dragDomWidth = dragDom.offsetWidth; // 对话框宽度
            const dragDomheight = dragDom.offsetHeight; // 对话框高度

            const minDragDomLeft = dragDom.offsetLeft;
            const maxDragDomLeft = screenWidth - dragDom.offsetLeft - dragDomWidth;

            const minDragDomTop = dragDom.offsetTop;
            const maxDragDomTop = screenHeight - dragDom.offsetTop - dragDomheight;


            // 获取到的值带px 正则匹配替换
            let styL = sty(dragDom, 'left');
            let styT = sty(dragDom, 'top');

            // 注意在ie中 第一次获取到的值为组件自带50% 移动之后赋值为px
            if (styL.includes('%')) {
                styL = +document.body.clientWidth * (+styL.replace(/\%/g, '') / 100);
                styT = +document.body.clientHeight * (+styT.replace(/\%/g, '') / 100);
            } else {
                styL = +styL.replace(/\px/g, '');
                styT = +styT.replace(/\px/g, '');
            };

            document.onmousemove = function (e) {
                // 通过事件委托，计算移动的距离 
                let left = e.clientX - disX;
                let top = e.clientY - disY;

                // 边界处理
                if (-(left) > minDragDomLeft) {
                    left = -(minDragDomLeft);
                } else if (left > maxDragDomLeft) {
                    left = maxDragDomLeft;
                }

                if (-(top) > minDragDomTop) {
                    top = -(minDragDomTop);
                } else if (top > maxDragDomTop) {
                    top = maxDragDomTop;
                }

                // 移动当前元素  
                dragDom.style.cssText += `;left:${left + styL}px;top:${top + styT}px;`;
            };

            document.onmouseup = function (e) {
                document.onmousemove = null;
                document.onmouseup = null;
            };
            return false
        }
    }
})

// 全局注册拖拽自定义指令
Vue.directive('drag', {
    bind: function (el, binding) {
        let dragBox = el; //获取当前元素
        dragBox.onmousedown = e => {
            let startTime = e.timeStamp;
            //算出鼠标相对元素的位置
            let disX = e.clientX - dragBox.offsetLeft;
            let disY = e.clientY - dragBox.offsetTop;
            document.onmousemove = e => {
                //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
                let left = e.clientX - disX;
                let top = e.clientY - disY;
                let xLeft;
                if (left < 0) {
                    xLeft = 0;
                } else if (left > window.innerWidth - dragBox.offsetWidth) {
                    xLeft = window.innerWidth - dragBox.offsetWidth;
                } else {
                    xLeft = left;
                }
                let xTop = top < 50 ? 50
                    : (top > window.innerHeight - dragBox.offsetHeight - 60) ?
                        window.innerHeight - dragBox.offsetHeight - 60 : top;
                //移动当前元素
                dragBox.style.left = xLeft + "px";
                dragBox.style.top = xTop + "px";
            };
            document.onmouseup = e => {
                let diffTime = e.timeStamp - startTime;
                if (diffTime < 150 && binding.value) {
                    binding.value();
                }
                //鼠标弹起来的时候不再移动
                document.onmousemove = null;
                //预防鼠标弹起来后还会循环（即预防鼠标放上去的时候还会移动）  
                document.onmouseup = null;
            };
        };
    }
})
// 全局注册拖拽自定义指令
Vue.directive('groupDrag', {
    bind: function (el, binding) {
        let dragBox = el; //获取当前元素
        dragBox.onmousedown = e => {
            let startTime = e.timeStamp;
            //算出鼠标相对元素的位置
            let disX = e.clientX - dragBox.offsetLeft;
            let disY = e.clientY - dragBox.offsetTop;
            dragBox.setAttribute('data-flag',false);
            document.onmousemove = e => {
                //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
                let left = e.clientX - disX;
                let top = e.clientY - disY;
                let xLeft;
                if (left < 0) {
                    xLeft = 0;
                } else if (left > window.innerWidth - dragBox.offsetWidth) {
                    xLeft = window.innerWidth - dragBox.offsetWidth;
                } else {
                    xLeft = left;
                }
                let xTop = top < 0 ? 0
                    : (top > window.innerHeight - dragBox.offsetHeight) ?
                        window.innerHeight - dragBox.offsetHeight : top;
                //移动当前元素
                dragBox.style.left = xLeft + "px";
                dragBox.style.top = xTop + "px";
            };
            document.onmouseup = e => {
                let diffTime = e.timeStamp - startTime;
                if (diffTime < 200) {
                    dragBox.setAttribute('data-flag',true);
                }
                if (diffTime < 150 && binding.value) {
                    binding.value();
                }
                //鼠标弹起来的时候不再移动
                document.onmousemove = null;
                //预防鼠标弹起来后还会循环（即预防鼠标放上去的时候还会移动）  
                document.onmouseup = null;
            };
        };
    }
})
// 防重复点击(指令实现)
Vue.directive('preventReClick', {
    bind (el, binding) {
        el.addEventListener('click', (self) => {
            let pointerEvents = el.style['pointer-events'];
            if (!pointerEvents || pointerEvents === 'auto') {
                el.style['pointerEvents'] = 'none';
                setTimeout(() => {
                    el.style['pointer-events'] = 'auto'
                }, parseInt(binding.value) || 1500)
            }
        })
    }
})
function getWatermarkContent(){
    let watermarkObj = CurrentUser.LoginConfig && CurrentUser.LoginConfig['water-mark']?CurrentUser.LoginConfig['water-mark']:{}
    watermarkContent.watermarkSwitch = !!parseInt(watermarkObj['water-mark-switch'])//是否展示水印
    if(!!watermarkContent.watermarkSwitch){
        if(!!parseInt(watermarkObj['water-mark-switch-text'])){//添加水印文字（姓名+手机号后四位）
        watermarkContent.watermarkText = (CurrentUser.username.length > 1 ? CurrentUser.username.substring(CurrentUser.username.length-2): CurrentUser.username)
        if(!!CurrentUser.phonenum){
            watermarkContent.watermarkText = watermarkContent.watermarkText + CurrentUser.phonenum.substring(CurrentUser.phonenum.length-4)
        }
        }
        if(!!parseInt(watermarkObj['water-mark-switch-date'])){//添加日期
            let date = new Date()
            let Y = date.getFullYear() + '-';
            let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
            let D = date.getDate()+1 < 10 ? '0'+ date.getDate() : date.getDate()
            watermarkContent.watermarkText = watermarkContent.watermarkText +' ' + Y + M + D
        }
        watermarkObj = rxVue.RX_Util.formatePhotourl(watermarkObj,'water-mark-pic') //添加图片
        watermarkContent.watermarkPic = watermarkObj['water-mark-pic']//添加图片
        watermarkContent.watermarkAngle = watermarkObj['water-mark-dip-angle']//水印倾斜度
        console.log('watermarkContent',watermarkContent)
    }
    return watermarkContent
}
function waterMarkEvents(eleNode,params){
    let watermarkContent = getWatermarkContent()
    console.log('watermarkContent',watermarkContent)
    if(!!watermarkContent.watermarkSwitch && (watermarkContent.watermarkText || watermarkContent.watermarkPic)){
        watermarkContent.watermarkObj = {
            watermark_parent_node: eleNode,
            watermark_txt: watermarkContent.watermarkText,  //水印的内容
            watermark_pic: watermarkContent.watermarkPic || '',
            watermark_x_space: 20,
            watermark_y_space: 20,
            watermark_width: watermarkContent.watermarkText.length * 16 > 200 ? watermarkContent.watermarkText.length * 16 - 80 : watermarkContent.watermarkText.length * 16,
            watermark_height: 120,
            watermark_angle: watermarkContent.watermarkAngle || 45,   //水印倾斜度数
            watermark_alpha: params.alpha || 0.1,               //水印透明度，要求设置在大于等于0.005
            watermark_color: params.fontColor || "#367EE8",
            watermark_fontsize: params.fontSize || "16px",
            watermark_font: params.fontFamily || 'pingFangSC-Regular'
        }
        watermark.init(watermarkContent.watermarkObj)
    }
}
// 水印(指令实现)
Vue.directive('waterMarker', {
    bind (el, binding) {
        waterMarkEvents(el, {...binding.value})
    },
    inserted(){
        if(!!watermarkContent.watermarkSwitch) watermark.load(watermarkContent.watermarkObj);
    }
})


